import React,{Component} from 'react';
import styled from 'styled-components';
import './Boxh.css';


const Styles = styled.div`

`;

class Boxh extends Component {
    constructor(props){
        super(props)
        this.state = {
          a : 2,
        };
       
    }

   
    render(){
        return (
            <Styles>
                <div style={{backgroundColor:"#FFFCFC",paddingTop:"15px",paddingBottom:"20px"}}>
                
                <div className= "oval" >
                <h1 style={{paddingTop:"3%",paddingBottom:"2%",color:"white"}}>Contraceptive Checklist Tool</h1>
                    </div>
                    <p style={{paddingLeft:"15%",paddingRight:"15%",paddingTop:"4%",paddingBottom:"10%"}}>Whether you have already decided to start a family or still in the process of considering starting one, this tool will help you keep track of the day-to-day factors that impact your fertility and chances of having a healthy baby.</p>
                </div>
                
                

            </Styles>
            

        )
    }


}



export default Boxh;