import React,{Component} from 'react';
import styled from 'styled-components';
import './Box0.css';
import { FcHighPriority } from 'react-icons/fc';
import { FcOk } from 'react-icons/fc';

const Styles = styled.div`
      .slider {
          width:100%;
          background:yellow;
      }
`;

class Box0 extends Component {
    constructor(props){
        super(props)
        this.state = {
          age : 0,
          ticka:false,
          weight:0,
          height:0,
          bmi :0,
          tickb:false,
          alcohol:0,
          cigarettes:0,
          yesage:false,
          yesbmi:false,
          yescig:false,
          yesalc:false,
        };
        this.handleOnChange = this.handleOnChange.bind(this)
        this.handleOnChangew = this.handleOnChangew.bind(this)
        this.handleOnChangeh = this.handleOnChangeh.bind(this)
        this.handleOnChangec = this.handleOnChangec.bind(this)
        this.handleOnChangea = this.handleOnChangea.bind(this)
        this.handlea1 = this.handlea1.bind(this)
        this.handlea0 = this.handlea0.bind(this)
        this.handleb1 = this.handleb1.bind(this)
        this.handleb0 = this.handleb0.bind(this)
        this.handlec1 = this.handlec1.bind(this)
        this.handlec0 = this.handlec0.bind(this)
        this.handleal1 = this.handleal1.bind(this)
        this.handleal0 = this.handleal0.bind(this)
    }

    handleOnChange = (e) => {
        this.setState({age:e.target.value , ticka:true})
    }
    handleOnChangew = (e) => {
        this.setState({weight:e.target.value})
        let h = this.state.height;
        h = h/100;
        let w = this.state.weight;
        let b = w/(h*h);
        this.setState({bmi:b , tickb:true}) 
        
    }
    handleOnChangeh = (e) => {
        this.setState({height:e.target.value})
        let h = this.state.height;
        h = h/100;
        
        let w = this.state.weight;
        let b = w/(h*h);
        b= Math.floor(b * 100) / 100 ;
        this.setState({bmi:b, tickb:true})  // here apply if to check bmi value and accordingly set tick
    }
    handleOnChangec = (e) => {
        this.setState({cigarettes:e.target.value})
    }
    handleOnChangea = (e) => {
        this.setState({alcohol:e.target.value})
    }
    handlea1(p) {
        this.setState({yesage:true})
    }
    handlea0(p) {
        this.setState({yesage:false})
    }
    handleb1(p) {
        this.setState({yesbmi:true})
    }
    handleb0(p) {
        this.setState({yesbmi:false})
    }
    handlec1(p) {
        this.setState({yescig:true})
    }
    handlec0(p) {
        this.setState({yescig:false})
    }
    handleal1(p) {
        this.setState({yesalc:true})
    }
    handleal0(p) {
        this.setState({yesalc:false})
    }
    render(){
        return (
            <Styles>
                <div style={{backgroundColor:"#BCD8DF"}}>
                 

                    <div className="row" style={{paddingTop:"15px"}}>

                        <div className="col-md-3">
                        <p style={{textAlign:"right",paddingBottom:"-10px"}}>Age :</p>
                        </div>
                        <div className="col-md-6">
                        <input type="range" min={0} max={50} value={this.state.age} onChange={this.handleOnChange} className="slider" />
                        </div>
                        <div className="col-md-1">
                        {this.state.age >35?<div style={{marginLeft:"-80px"}}><a  onMouseOver={this.handlea1} onMouseLeave={this.handlea0}><FcHighPriority />{this.state.yesage?<span className="tooltiptext"><div><p>Agel!!</p><p>As women become older, the ability to conceive also decreases. Fertility starts decreasing from 30 years and starts to significantly decrease after 35 years</p></div></span>:null}</a></div> :null}
                        {this.state.age <=35 && this.state.ticka?<div style={{marginLeft:"-80px"}}><a  ><FcOk /> </a></div> :null}
                        
                        </div>
                        <div className="col-md-2">
                        <input type="text"  value={this.state.age} style={{width:"35px",textAlign:"center",marginLeft:"-120px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>Years</p>
                        </div>

                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">

                        <div className="col-md-3">
                        <p style={{textAlign:"right"}}>Weight :</p>
                        </div>
                        <div className="col-md-6">
                            <input type="range" min={0} max={200} value={this.state.weight} onChange={this.handleOnChangew} className="slider"/>
                        </div>
                        <div className="col-md-1">
                          
                        </div>
                        <div className="col-md-2">
                        <input type="text"  value={this.state.weight} style={{width:"35px",textAlign:"center",marginLeft:"-140px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>Kg</p>
                        </div>

                    </div>
                    <div className="row">

                        <div className="col-md-3">
                        <p style={{textAlign:"right"}}>Height :</p>
                        </div>
                        <div className="col-md-6">
                        <input type="range" min={0} max={250} value={this.state.height} onChange={this.handleOnChangeh} className="slider"></input>
                        </div>
                        <div className="col-md-1">
                        </div>
                        <div className="col-md-2">
                        <input type="text"  value={this.state.height} style={{width:"35px",textAlign:"center",marginLeft:"-140px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>cm</p>
                        </div>

                    </div>
                    <div className="row">

                        <div className="col-md-3">
                        <p style={{textAlign:"right"}}>Your Body Mass Index (BMI) :</p>
                        </div>
                        <div className="col">
                        {this.state.bmi}
                        </div>
                        <div className="col-md-1">
                        {(this.state.bmi >25 || this.state.bmi <18) && this.state.tickb ?<div style={{marginLeft:"-80px"}}><a  onMouseOver={this.handleb1} onMouseLeave={this.handleb0}><FcHighPriority />{this.state.yesbmi?<span className="tooltiptext"><div><p>Alcohol!!</p><p>Heavy drinking impacts fertility, increases the time taken to conceive and increases the risk of complications in your pregnancy</p></div></span>:null}</a></div> :<div style={{marginLeft:"-80px"}}><a  ><FcOk /> </a></div>
                        }
                        
                        </div>

                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"1%"}} />
                    <div className="row">

                        <div className="col-md-3">
                        <p style={{textAlign:"right"}}>How many cigarettes do you smoke a day?</p>
                        </div>
                        <div className="col-md-6">
                        <input type="range" min={0} max={20} value={this.state.cigarettes} onChange={this.handleOnChangec} className="slider"></input>
                        </div>
                        <div className="col-md-1">
                        {this.state.cigarettes >0?<div style={{marginLeft:"-80px"}}><a  onMouseOver={this.handlec1} onMouseLeave={this.handlec0}><FcHighPriority />{this.state.yescig?<span className="tooltiptext"><div><p>Alcohol!!</p><p>Heavy drinking impacts fertility, increases the time taken to conceive and increases the risk of complications in your pregnancy</p></div></span>:null}</a></div> :null}
                        </div>
                        <div className="col-md-2">
                        <input type="text"  value={this.state.cigarettes} style={{width:"35px",textAlign:"center",marginLeft:"-95px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>cigarettes</p>
                        </div>

                    </div>
                    <div className="row">
                        <div className="col-md-3">
                        <p style={{textAlign:"right"}}>How many glasses of alcohol do you consume in a week?</p>
                        </div>
                        <div className="col-md-6">
                        <input type="range" min={0} max={20} value={this.state.alcohol} onChange={this.handleOnChangea} className="slider"></input>
                        </div>
                        <div className="col-md-1">
                        {this.state.alcohol >0?<div style={{marginLeft:"-80px"}}><a  onMouseOver={this.handleal1} onMouseLeave={this.handleal0}><FcHighPriority />{this.state.yesalc?<span className="tooltiptext"><div><p>Alcohol!!</p><p>Heavy drinking impacts fertility, increases the time taken to conceive and increases the risk of complications in your pregnancy</p></div></span>:null}</a></div> :null}
                        </div>
                        <div className="col-md-2">
                        <input type="text"  value={this.state.alcohol} style={{width:"35px",textAlign:"center",marginLeft:"-200px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>std. amount</p>
                        </div>

                    </div>







                    
                    
                    
                    
                    


                
                    
                </div>
                
                

            </Styles>
            

        )
    }


}



export default Box0;

