import React,{Component} from 'react';
import styled from 'styled-components';
import Slider from './Slider';
import './Box1.css';
import { FcHighPriority } from 'react-icons/fc';
import { FcOk } from 'react-icons/fc';

const Styles = styled.div`
.slider {
          width:80%;
          background:yellow;
      }
`;

class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
            
            
            selectedOption1 : "",
            selectedOption1a : "",
            selectedOption2 : "",
            selectedOption3 : "",
            selectedOption4 : "",
            selectedOption5 : "",
            start:0,
            month:0,
            partner:0,
            summarybutton:false,
            click1:false,
            click2:false,
            click3:false,
            click4:false,
            click5:false,
            click6:false,
            click7:false,
            click8:false,
            click9:false,
            click10:false,
            yesc1:false,
            tickc1:false,
            
        };
       
        this.handleOptionChange1 = this.handleOptionChange1.bind(this)
        this.handleOptionChange1a = this.handleOptionChange1a.bind(this)
        this.handleOptionChange2 = this.handleOptionChange2.bind(this)
        this.handleOptionChange3 = this.handleOptionChange3.bind(this)
        this.handleOptionChange4 = this.handleOptionChange4.bind(this)
        this.handleOptionChange5 = this.handleOptionChange5.bind(this)
        this.handleOnChangestart = this.handleOnChangestart.bind(this)
        this.handleOnChangepartner = this.handleOnChangepartner.bind(this)
        this.handleOnChangemonth = this.handleOnChangemonth.bind(this)
        this.handleClickbutton = this.handleClickbutton.bind(this)
        this.handleClick1 = this.handleClick1.bind(this)
        this.handleClick2 = this.handleClick2.bind(this)
        this.handleClick3 = this.handleClick3.bind(this)
        this.handleClick4 = this.handleClick4.bind(this)
        this.handleClick5 = this.handleClick5.bind(this)
        this.handleClick6 = this.handleClick6.bind(this)
        this.handleClick7 = this.handleClick7.bind(this)
        this.handleClick8 = this.handleClick8.bind(this)
        this.handleClick9 = this.handleClick9.bind(this)
        this.handleClick10 = this.handleClick10.bind(this)
        this.handlec1 = this.handlec1.bind(this)
        this.handlec10 = this.handlec10.bind(this)

    }
  
    
    handleOptionChange1(changeEvent) {
        this.setState({selectedOption1: changeEvent.target.value});
    }
    handleOptionChange1a(changeEvent) {
        this.setState({selectedOption1a: changeEvent.target.value});
    }
    handleOptionChange2(changeEvent) {
        this.setState({selectedOption2: changeEvent.target.value});
    }
    handleOptionChange3(changeEvent) {
        this.setState({selectedOption3: changeEvent.target.value});
    }
    handleOptionChange4(changeEvent) {
        this.setState({selectedOption4: changeEvent.target.value});
    }
    handleOptionChange5(changeEvent) {
        this.setState({selectedOption5: changeEvent.target.value});
    }
    handleOnChangestart = (e) => {
        this.setState({start:e.target.value})
    }
    handleOnChangepartner = (e) => {
        this.setState({partner:e.target.value, tickc1:true})
    }
    handleOnChangemonth = (e) => {
        this.setState({month:e.target.value})
    }
    handleClickbutton(p){
        this.setState({summarybutton:true})
    }
    handleClick1(q){
        this.setState({click1:true})
    }
    handleClick2(q){
        this.setState({click2:true})
    }
    handleClick3(q){
        this.setState({click3:true})
    }
    handleClick4(q){
        this.setState({click4:true})
    }
    handleClick5(q){
        this.setState({click5:true})
    }
    handleClick6(q){
        this.setState({click6:true})
    }
    handleClick7(q){
        this.setState({click7:true})
    }
    handleClick8(q){
        this.setState({click8:true})
    }
    handleClick9(q){
        this.setState({click9:true})
    }
    handleClick10(q){
        this.setState({click10:true})
    }
    handlec1(p) {
        this.setState({yesc1:true})
    }
    handlec10(p) {
        this.setState({yesc1:false})
    }

    render(){
        return (
            <Styles>
            <div>
            

                <div className="row">
                    <div className="col">
                    <p style={{textAlign:"left"}}>Are you trying to concieve?</p>
                    </div>
                    <div className="col"  style={{textAlign:"left"}}>
                    <form>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="yes" 
                                            checked={this.state.selectedOption1 === "yes"} 
                                            onChange={this.handleOptionChange1} />
                                Yes
                            </label>
                        </div>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="no" 
                                checked={this.state.selectedOption1 === "no"} 
                                onChange={this.handleOptionChange1} />
                                No
                            </label>
                        </div>
                            
                    </form>
                    </div>

                </div>
                {this.state.selectedOption1=="yes"?<div className="container">

                    <div className="row">
                        <div className="col">
                        <p style={{textAlign:"left"}}>How many months since your started?</p>
                        </div>
                        <div className="col" style={{textAlign:"left"}}>
                        <input type="range" min={0} max={24} value={this.state.start} onChange={this.handleOnChangestart} className="slider" />
                        </div>
                        <div className="col">
                        <input type="text"  value={this.state.start} style={{width:"35px",textAlign:"center",marginLeft:"-120px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        
                        </div>
                    </div>
                    <div className="row">
                        <div className="col">
                        <p style={{textAlign:"left"}}>How old is your partner?</p>
                        </div>
                        <div className="col" style={{textAlign:"left"}}>
                        <input type="range" min={0} max={70} value={this.state.partner} onChange={this.handleOnChangepartner} className="slider" />
                        </div>
                        <div className="col">
                        {this.state.partner >45?<div ><a  onMouseOver={this.handlec1} onMouseLeave={this.handlec10}><FcHighPriority />{this.state.yesc1?<span className="tooltiptext"><div><p>Agel!!</p><p>If your partner is older than 45 years, it make increase the time it take to get pregnant</p></div></span>:null}</a></div> :null}
                        {this.state.partner <=45 && this.state.tickc1?<div style={{marginLeft:"-80px"}}><a  ><FcOk /> </a></div> :null}
                        </div>
                        <div className="col">
                        <input type="text"  value={this.state.partner} style={{width:"35px",textAlign:"center",marginLeft:"-120px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        <p style={{display:"inline",paddingLeft:"4%"}}>Years</p>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col">
                        <p style={{textAlign:"left"}}>How often do you have sex per month?</p>
                        </div>
                        <div className="col" style={{textAlign:"left"}}>
                        <input type="range" min={0} max={30} value={this.state.month} onChange={this.handleOnChangemonth} className="slider" />
                        </div>
                        <div className="col">
                        <input type="text"  value={this.state.month} style={{width:"35px",textAlign:"center",marginLeft:"-120px",height:"25px",display: "inline",borderRadius:"5px"}}></input>
                        
                        </div>
                        
                    </div>
                    <div className="row">
                            <div className="col">
                    
                        <p style={{textAlign:"left"}}>Are you having sex at the right time?</p>
                        </div>
                        <div className="col"  style={{textAlign:"left"}}>
                        <form>
                                <div className="radio"style={{display:"inline"}}>
                                    <label>
                                        <input type="radio" value="yes" 
                                                    checked={this.state.selectedOption1a === "yes"} 
                                                    onChange={this.handleOptionChange1a}  />
                                        Yes
                                    </label>
                                </div>
                                <div className="radio" style={{display:"inline"}}>
                                    <label>
                                        <input type="radio" value="no" 
                                        checked={this.state.selectedOption1a === "no"} 
                                        onChange={this.handleOptionChange1a}  />
                                        No
                                    </label>
                                </div>
                                <div className="radio" style={{display:"inline"}}>
                                    <label>
                                        <input type="radio" value="unsure" 
                                        checked={this.state.selectedOption1a === "unsure"} 
                                        onChange={this.handleOptionChange1a} style={{display:"inline"}} />
                                        Unsure
                                    </label>
                                </div>
                            </form>

                        </div>
                                
                    </div>

                

                </div>:null}

                <hr style={{width:"100%",color:"black",border:"1px solid black"}} />
                
                <div className="row">
                    <div className="col">
                    <p  style={{textAlign:"left",marginLeft:"100px"}}>Have you been tested for STIs?</p>
                    </div>
                    <div className="col"  style={{textAlign:"left"}}>
                    <form>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="yes" 
                                            checked={this.state.selectedOption2 === "yes"} 
                                            onChange={this.handleOptionChange2} />
                                Yes
                            </label>
                        </div>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="no" 
                                checked={this.state.selectedOption2 === "no"} 
                                onChange={this.handleOptionChange2} />
                                No
                            </label>
                        </div>
                            
                    </form>
                    </div>
                </div>
                {this.state.selectedOption2=="yes"?<div className="container">
                <div className="row">
                <div className="col">
                    <p  style={{textAlign:"left"}}>Have you tested positive?</p>
                </div>
                <div className="col"  style={{textAlign:"left"}}>
                <form>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="yes" 
                                            checked={this.state.selectedOption3 === "yes"} 
                                            onChange={this.handleOptionChange3} style={{display:"inline"}} />
                                Yes
                            </label>
                        </div>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="no" 
                                checked={this.state.selectedOption3 === "no"} 
                                onChange={this.handleOptionChange3} style={{display:"inline"}} />
                                No
                            </label>
                        </div>
                            
                    </form>

                </div>
                </div>

                </div>:null}

                <hr style={{width:"100%",color:"black",border:"1px solid black"}} />
                <div className="row">
                    <div className="col">
                    <p>Are your periods regular?</p>
                    </div>
                    <div className="col"  style={{textAlign:"left"}}>
                    <form>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="yes" 
                                            checked={this.state.selectedOption4 === "yes"} 
                                            onChange={this.handleOptionChange4} />
                                Yes
                            </label>
                        </div>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="no" 
                                checked={this.state.selectedOption4 === "no"} 
                                onChange={this.handleOptionChange4} />
                                No
                            </label>
                        </div>
                            
                    </form>
                    
                    </div>
                </div>
                <hr style={{width:"100%",border:"1px solid black"}} />
                <div className="row">
                <div className="col">
                Are you using contraception?
                </div>
                <div className="col"  style={{textAlign:"left"}}>
                <form>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="yes" 
                                            checked={this.state.selectedOption5 === "yes"} 
                                            onChange={this.handleOptionChange5} />
                                Yes
                            </label>
                        </div>
                        <div className="radio" style={{display:"inline"}}>
                            <label>
                                <input type="radio" value="no" 
                                checked={this.state.selectedOption5 === "no"} 
                                onChange={this.handleOptionChange5} />
                                No
                            </label>
                        </div>
                            
                    </form>

                </div>

                
                </div>
                <hr style={{width:"100%",color:"black",border:"1px solid black"}} />
                <div className="row">
                    <div className="col">
                    <p>Do you have any of the below conditions?</p>
                    </div>
                    <div className="col" style={{textAlign:"left"}}>
                            <input type="checkbox" value="1" onClick={this.handleClick1}></input>
                            <label for="checkbox1">Polycystic Ovarian Syndrome (PCOS)</label>
                            
                            <br />
                            <input type="checkbox"  value="2" onClick={this.handleClick2}></input>
                            <label for="checkbox2">Diabetes</label><br />
                            <input type="checkbox" value="3" onClick={this.handleClick3}></input>
                            <label for="checkbox3">Thyroid disorders</label><br />
                            <input type="checkbox" value="4" onClick={this.handleClick4}></input>
                            <label for="checkbox4">Cancer</label><br />
                            <input type="checkbox" value="5" onClick={this.handleClick5}></input>
                            <label for="checkbox5">Endometriosis</label><br />
                    </div>
                </div>
                <hr style={{width:"100%",color:"black",border:"1px solid black"}} />
                
                <div className="row">
                    <div className="col">
                    <p>Are you taking any long term medications for any of these conditions?</p>
                    </div>
                    <div className="col"  style={{textAlign:"left"}}>
                            <input type="checkbox" value="1" onClick={this.handleClick6}></input>
                            <label for="vehicle1">Seizures</label><br />
                            <input type="checkbox" value="2" onClick={this.handleClick7}></input>
                            <label for="vehicle2">Epilepsy</label><br />
                            <input type="checkbox" value="3" onClick={this.handleClick8}></input>
                            <label for="vehicle3">Acne</label><br />
                            <input type="checkbox"  value="4" onClick={this.handleClick9}></input>
                            <label for="vehicle3">Antibiotics</label><br />
                            <input type="checkbox" value="5" onClick={this.handleClick10}></input>
                            <label for="vehicle3">High Blood Pressure</label><br />
                    </div>
                </div>






                <button className="myButton" style={{marginLeft:"45%",marginTop:"2%"}} onClick ={()=> this.handleClickbutton()}>Display Summary</button>

                {this.state.summarybutton?<div>
                    <table className="customers">
                        <tr>
                            <th>Your Fertility Factors</th>
                            <th>Your Response</th>
                            <th>Our Feedback</th>
                        </tr>
                        <tr>
                            <td>Are you trying to conceive?</td>
                            <td>{this.state.selectedOption1}</td>
                            <td>-</td>
                        </tr>
                        <tr>
                            <td>How many months since your started?</td>
                            <td>{this.state.start}</td>
                            <td>{}</td>
                        </tr>
                        <tr>
                            <td>How old is your partner?</td>
                            <td>{this.state.partner}</td>
                            <td>{this.state.partner>45?<p>If your partner is older than 45 years, it make increase the time it take to get pregnant</p>:null}</td>
                        </tr>
                        <tr>
                            <td>How often do you have sex per month?</td>
                            <td>{this.state.month}</td>
                            <td>UK</td>
                        </tr>
                        <tr>
                            <td>Are you having sex at the right time?</td>
                            <td>{this.state.selectedOption1a}</td>
                            <td>Canada</td>
                        </tr>
                        <tr>
                            <td>Have you been tested for STIs? </td>
                            <td>{this.state.selectedOption2}</td>
                            <td>Italy</td>
                        </tr>
                        <tr>
                            <td>Have you been tested positive for STIs? </td>
                            <td>{this.state.selectedOption3}</td>
                            <td>Italy</td>
                        </tr>
                        <tr>
                            <td>Are your periods regular? </td>
                            <td>{this.state.selectedOption4}</td>
                            <td>Italy</td>
                        </tr>
                        <tr>
                            <td>Are you using contraception?</td>
                            <td>{this.state.selectedOption5}</td>
                            <td>Italy</td>
                        </tr>
                        <tr>
                            <td>Do you have any of the below conditions?</td>
                            <td  style={{textAlign:"center"}}>
                                {this.state.click1?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Polycystic Ovarian Syndrome (PCOS)
                                </div>:null}
                                {this.state.click2?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Diabetes
                                </div>:null}
                                {this.state.click3?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Thyroid disorders
                                </div>:null}
                                {this.state.click4?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Cancer
                                </div>:null}
                                {this.state.click5?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Endometriosis
                                </div>:null}
                                
                                
                            </td>
                            <td>Italy</td>
                        </tr>
                        <tr>
                            <td>Are you taking any long term medications for any of these conditions?</td>
                            <td  style={{textAlign:"center"}}>
                                {this.state.click6?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Seizures
                                </div>:null}
                                {this.state.click7?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Epilepsy
                                </div>:null}
                                {this.state.click8?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Acne
                                </div>:null}
                                {this.state.click9?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                Antibiotics
                                </div>:null}
                                {this.state.click10?<div className="row" style={{textAlign:"center",marginLeft:"50px"}}>
                                High Blood Pressure
                                </div>:null}
                                
                                
                            </td>
                            <td>Italy</td>
                        </tr>
                        </table>


                </div>:null}
                <div style={{height:"300px",backgroundColor:"gray"}}>

                </div>


            </div>
            </Styles>

        )
    }


}
